.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 30px;
  z-index: 1;

  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.nav_black {
  background-color: #111;
}

.nav_logo {
  position: fixed;
  left: 20;
  width: 80px;
  object-fit: contain;
}
a {
  text-decoration: none;
}
.nav_avtar {
  position: fixed;
  right: 20px;
  cursor: pointer;
  color: #efefef;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgba(255, 0, 0, 0.8);
}
.nav_avtar:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}
